import React from 'react';
import Link from 'gatsby-link';
import YouTube from 'react-youtube';
import Textloop from '../components/textloop';
//import HeroLogo from '../images/cross-stitch-site-name.png';
//L24: <h1><img src={HeroLogo} alt="Sysforge Lab" title="Sysforge Lab" /></h1>
//L29: <YouTube videoId='RtEwlMiJoug'/>
import SFMobLogo from '../images/salesforce-logo-mobile.png';
import GitHubLogo from '../images/GitHub-Mark-22px.png';
import LinkedinLogo from '../images/linkedIn.png';
import GoogleLogo from '../images/google-play-store.png';
import AppleLogo from '../images/apple-app-store.png';
import Snack from '../components/Snack'

export const Masthead = ({props}) =>
(
    <div className="jumbotron">
        <br/>
        <div className="container text-center">
            <div className="row vertical-align">
                <div className="col-sm-6">
                    <p className="heading-content">IoT Barcode Mobility</p>
                    <Snack/>
                    <a href="https://play.google.com/store/apps/details?id=host.exp.exponent&amp;referrer=www" 
                        target="_blank">
                        <img src={GoogleLogo} alt="Google Play Store" height="40" width="136"/></a>
                    <a href="https://itunes.apple.com/app/apple-store/id982107779?ct=www&amp;mt=8" 
                        target="_blank">
                        <img src={AppleLogo} alt="Apple Play Store" height="40" width="136"/></a>
                </div>
                <br/>
                <div className="col-sm-6">
                    <div className="youtube-embed-container">
                        <div className="embed-responsive embed-responsive-16by9">
                            <YouTube videoId='RtEwlMiJoug'/>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <br/>
        <div className="container breadcrumb-container">
            <div className="row">
                <div className="caption-content col-sm-6">
                    <Textloop springConfig={{ stiffness: 180, damping: 8 }}
                        children={[
                            "I'm Cloud Solution Architect since 12+ Years..",
                            "Force.com Sales, Services, Community Cloud",
                            "Polyglot - Apex, Sql, Scripts, Kotin, Java, ESB",
                            "OData, Informatica Cloud & API Integration",
                            "SSO, OAuth, OpenID, 2Way SSL, SAML token",
                            "Process/ Build Automation, Offline Data Sync",
                            "Data Migration, Terminal, Outbound Message",
                            "Stay Tuned to know !MADHAV SHANDILYA!"
                        ]}
                    />
                </div>
            </div>
        </div>
    </div>
);

export default Masthead;



